@import url(https://fonts.googleapis.com/css?family=Nunito);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-head {
  background-color: #fff;
  top: 0px;
  padding: 1em;
}
/*# sourceMappingURL=AppHead.css.map */
.app-popular-repo {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: none;
  height: 100%;
}

.app-results-feild {
  display: flex;
  justify-content: none;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  box-shadow: 1px 1px 5px #64646459;
  width: 55%;
  height: 100%;
  min-height: 100vh;
  margin-top: 0%;
  padding: 1em;
  font-size: 1.3em;
}

.secondary a {
  color: red;
  text-decoration: none;
}

.repo-name {
  color: red;
  text-decoration: none;
}

.third {
  color: #666666;
}

.repo-wrapper {
  padding: 0.5em 2.5em;
  font-size: 0.8em;
}

.repo-wrapper:hover {
  background-color: #ffe1e1;
}

.repo-title {
  display: flex;
  justify-content: space-between;
  padding: 2% 0;
}

.repo-details,
.repo-copy {
  padding: 2% 0;
}

.repo-header-title {
  width: 90%;
  padding: 0 2em;
}
/*# sourceMappingURL=PopRepo.css.map */
.app-body-search {
  background-color: #fff;
  box-shadow: 2px 2px 50px #64646459;
  width: 75%;
  height: 25vh;
  z-index: 100;
}

.app-body-search-wrapper {
  display: flex;
  justify-content: left;
  flex-direction: column;
  align-items: none;
  padding: 3em 0 0 0;
  width: 85%;
}

.search-head {
  text-align: left;
  padding-left: 10%;
}

form {
  width: 100%;
  padding-left: 5%;
}

input {
  width: 70%;
  border-radius: 5px;
  height: 35px;
  border: 0.5px solid #cfcfcf;
  font-size: 1.3em;
  padding-left: 10px;
  color: #31313159;
}

form > button {
  width: 20%;
  height: 45px;
  border-radius: 15px;
  border: 0px;
  background-color: #fc5151;
  color: #fff;
  font-size: 1.2em;
}

.repo-results-wrapper {
  display: flex;
  justify-content: none;
  flex-direction: column;
  align-items: none;
  text-align: left;
  background-color: #fff;
  width: 100%;
  margin: 1em 0;
}

.resume {
  text-align: left;
  width: 90%;
  padding: 0 1em;
}
/*# sourceMappingURL=Resume.css.map */
.app-body {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background-color: #efefef;
  width: 100%;
}

.app-body-head {
  padding: 1em;
}
/*# sourceMappingURL=AppBody.css.map */
.exozet-app {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: none;
  width: 100vw;
  font-family: 'Nunito', sans-serif;
  font-size: 1em;
}
/*# sourceMappingURL=App.css.map */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

